
/**
 * This class should not be used directly by an application developer. Instead, use
 * {\@link Location}.
 * 
 * `PlatformLocation` encapsulates all calls to DOM apis, which allows the Router to be platform
 * agnostic.
 * This means that we can have different implementation of `PlatformLocation` for the different
 * platforms
 * that angular supports. For example, the default `PlatformLocation` is {\@link
 * BrowserPlatformLocation},
 * however when you run your app in a WebWorker you use {\@link WebWorkerPlatformLocation}.
 * 
 * The `PlatformLocation` class is used directly by all implementations of {\@link LocationStrategy}
 * when
 * they need to interact with the DOM apis like pushState, popState, etc...
 * 
 * {\@link LocationStrategy} in turn is used by the {\@link Location} service which is used directly
 * by
 * the {\@link Router} in order to navigate between routes. Since all interactions between {\@link
 * Router} /
 * {\@link Location} / {\@link LocationStrategy} and DOM apis flow through the `PlatformLocation`
 * class
 * they are all platform independent.
 * 
 * \@stable
 * @abstract
 */
export abstract class PlatformLocation {
/**
 * @abstract
 * @return {?}
 */
getBaseHrefFromDOM() {}
/**
 * @abstract
 * @param {?} fn
 * @return {?}
 */
onPopState(fn: LocationChangeListener) {}
/**
 * @abstract
 * @param {?} fn
 * @return {?}
 */
onHashChange(fn: LocationChangeListener) {}
/**
 * @return {?}
 */
get pathname(): string { return null; }
/**
 * @return {?}
 */
get search(): string { return null; }
/**
 * @return {?}
 */
get hash(): string { return null; }
/**
 * @abstract
 * @param {?} state
 * @param {?} title
 * @param {?} url
 * @return {?}
 */
replaceState(state: any, title: string, url: string) {}
/**
 * @abstract
 * @param {?} state
 * @param {?} title
 * @param {?} url
 * @return {?}
 */
pushState(state: any, title: string, url: string) {}
/**
 * @abstract
 * @return {?}
 */
forward() {}
/**
 * @abstract
 * @return {?}
 */
back() {}
}

/**
 * A serializable version of the event from onPopState or onHashChange
 *
 * @experimental
 */
export interface LocationChangeEvent { type: string; }

/**
 * @experimental
 */
export interface LocationChangeListener { (e: LocationChangeEvent): any; }

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
